var getRoomsFromServer, getRoomsFromServerDelayed, getUsersFromServer, getUsersFromServerDelayed;

this.filteredUsersMemory = new Mongo.Collection(null);

Meteor.startup(function() {
  return Tracker.autorun(function() {
    var messageUsers, uniqueMessageUsersControl;
    if ((Meteor.user() == null) || (Session.get('openedRoom') == null)) {
      return;
    }
    filteredUsersMemory.remove({});
    messageUsers = RocketChat.models.Messages.find({
      rid: Session.get('openedRoom'),
      'u.username': {
        $ne: Meteor.user().username
      }
    }, {
      fields: {
        'u.username': 1,
        'u.name': 1,
        ts: 1
      },
      sort: {
        ts: -1
      }
    }).fetch();
    uniqueMessageUsersControl = {};
    return messageUsers.forEach(function(messageUser) {
      if (uniqueMessageUsersControl[messageUser.u.username] == null) {
        uniqueMessageUsersControl[messageUser.u.username] = true;
        return filteredUsersMemory.upsert(messageUser.u.username, {
          _id: messageUser.u.username,
          username: messageUser.u.username,
          name: messageUser.u.name,
          status: Session.get('user_' + messageUser.u.username + '_status') || 'offline',
          ts: messageUser.ts
        });
      }
    });
  });
});

getUsersFromServer = (function(_this) {
  return function(filter, records, cb) {
    var messageUsers;
    messageUsers = _.pluck(records, 'username');
    return Meteor.call('spotlight', filter, messageUsers, {
      users: true
    }, function(err, results) {
      var i, len, ref, result;
      if (err != null) {
        return console.error(err);
      }
      if (results.users.length > 0) {
        ref = results.users;
        for (i = 0, len = ref.length; i < len; i++) {
          result = ref[i];
          if (records.length < 5) {
            records.push({
              _id: result.username,
              username: result.username,
              status: 'offline',
              sort: 3
            });
          }
        }
        records = _.sortBy(records, 'sort');
        return cb(records);
      }
    });
  };
})(this);

getRoomsFromServer = (function(_this) {
  return function(filter, records, cb) {
    return Meteor.call('spotlight', filter, null, {
      rooms: true
    }, function(err, results) {
      var i, len, ref, room;
      if (err != null) {
        return console.error(err);
      }
      if (results.rooms.length > 0) {
        ref = results.rooms;
        for (i = 0, len = ref.length; i < len; i++) {
          room = ref[i];
          if (records.length < 5) {
            records.push(room);
          }
        }
        return cb(records);
      }
    });
  };
})(this);

getUsersFromServerDelayed = _.throttle(getUsersFromServer, 500);

getRoomsFromServerDelayed = _.throttle(getRoomsFromServer, 500);

Template.messagePopupConfig.helpers({
  popupUserConfig: function() {
    var config, self, template;
    self = this;
    template = Template.instance();
    config = {
      title: t('People'),
      collection: filteredUsersMemory,
      template: 'messagePopupUser',
      getInput: self.getInput,
      textFilterDelay: 200,
      trigger: '@',
      suffix: ' ',
      getFilter: function(collection, filter, cb) {
        var all, exp, here, items, messageUsers, ref;
        exp = new RegExp("" + (RegExp.escape(filter)), 'i');
        items = filteredUsersMemory.find({
          ts: {
            $exists: true
          },
          $or: [
            {
              username: exp
            }, {
              name: exp
            }
          ]
        }, {
          limit: 5,
          sort: {
            ts: -1
          }
        }).fetch();
        if (items.length < 5 && (filter != null ? filter.trim() : void 0) !== '') {
          messageUsers = _.pluck(items, 'username');
          Meteor.users.find({
            $and: [
              {
                $or: [
                  {
                    username: exp
                  }, {
                    name: exp
                  }
                ]
              }, {
                username: {
                  $nin: [(ref = Meteor.user()) != null ? ref.username : void 0].concat(messageUsers)
                }
              }
            ]
          }, {
            limit: 5 - messageUsers.length
          }).fetch().forEach(function(item) {
            return items.push({
              _id: item.username,
              username: item.username,
              name: item.name,
              status: item.status,
              sort: 1
            });
          });
        }
        if (items.length < 5 && (filter != null ? filter.trim() : void 0) !== '') {
          getUsersFromServerDelayed(filter, items, cb);
        }
        all = {
          _id: 'all',
          username: 'all',
          system: true,
          name: t('Notify_all_in_this_room'),
          compatibility: 'channel group',
          sort: 4
        };
        exp = new RegExp("(^|\\s)" + (RegExp.escape(filter)), 'i');
        if (exp.test(all.username) || exp.test(all.compatibility)) {
          items.push(all);
        }
        here = {
          _id: 'here',
          username: 'here',
          system: true,
          name: t('Notify_active_in_this_room'),
          compatibility: 'channel group',
          sort: 4
        };
        if (exp.test(here.username) || exp.test(here.compatibility)) {
          items.push(here);
        }
        return items;
      },
      getValue: function(_id) {
        return _id;
      }
    };
    return config;
  },
  popupChannelConfig: function() {
    var config, self, template;
    self = this;
    template = Template.instance();
    config = {
      title: t('Channels'),
      collection: RocketChat.models.Subscriptions,
      trigger: '#',
      suffix: ' ',
      template: 'messagePopupChannel',
      getInput: self.getInput,
      getFilter: function(collection, filter, cb) {
        var exp, records;
        exp = new RegExp(filter, 'i');
        records = collection.find({
          name: exp,
          t: {
            $in: ['c', 'p']
          }
        }, {
          limit: 5,
          sort: {
            ls: -1
          }
        }).fetch();
        if (records.length < 5 && (filter != null ? filter.trim() : void 0) !== '') {
          getRoomsFromServerDelayed(filter, records, cb);
        }
        return records;
      },
      getValue: function(_id, collection, records) {
        var ref;
        return (ref = _.findWhere(records, {
          _id: _id
        })) != null ? ref.name : void 0;
      }
    };
    return config;
  },
  popupSlashCommandsConfig: function() {
    var config, self, template;
    self = this;
    template = Template.instance();
    config = {
      title: t('Commands'),
      collection: RocketChat.slashCommands.commands,
      trigger: '/',
      suffix: ' ',
      triggerAnywhere: false,
      template: 'messagePopupSlashCommand',
      getInput: self.getInput,
      getFilter: function(collection, filter) {
        var command, commands, item;
        commands = [];
        for (command in collection) {
          item = collection[command];
          if (command.indexOf(filter) > -1) {
            commands.push({
              _id: command,
              params: item.params ? TAPi18n.__(item.params) : '',
              description: TAPi18n.__(item.description)
            });
          }
        }
        commands = commands.sort(function(a, b) {
          return a._id > b._id;
        });
        commands = commands.slice(0, 11);
        return commands;
      }
    };
    return config;
  },
  emojiEnabled: function() {
    return RocketChat.emoji != null;
  },
  popupEmojiConfig: function() {
    var config, self, template;
    if (RocketChat.emoji != null) {
      self = this;
      template = Template.instance();
      config = {
        title: t('Emoji'),
        collection: RocketChat.emoji.list,
        template: 'messagePopupEmoji',
        trigger: ':',
        prefix: '',
        suffix: ' ',
        getInput: self.getInput,
        getFilter: function(collection, filter, cb) {
          var key, ref, regExp, results, value;
          results = [];
          key = ':' + filter;
          if (((ref = RocketChat.emoji.packages.emojione) != null ? ref.asciiList[key] : void 0) || filter.length < 2) {
            return [];
          }
          regExp = new RegExp('^' + RegExp.escape(key), 'i');
          for (key in collection) {
            value = collection[key];
            if (results.length > 10) {
              break;
            }
            if (regExp.test(key)) {
              results.push({
                _id: key,
                data: value
              });
            }
          }
          results.sort(function(a, b) {
            if (a._id < b._id) {
              return -1;
            }
            if (a._id > b._id) {
              return 1;
            }
            return 0;
          });
          return results;
        }
      };
    }
    return config;
  }
});
