katexSyntax = function() {
  if (RocketChat.katex.katex_enabled()) {
    if (RocketChat.katex.dollar_syntax_enabled()) {
      return "$$KaTeX$$";
    }
    if (RocketChat.katex.parenthesis_syntax_enabled()) {
      return "\\[KaTeX\\]";
    }
  }
  return false;
};

